.nav-wrapper {
}
.nav {
  display: flex;
  justify-content: space-between;
  height: 70px;
  align-items: center;
  padding: 0 3rem;
  position: fixed;
  top: 0;
  z-index: 20;
  width: 100%;
  transition: all 0.2s ease;
}
.show {
  background-color: #1f1f1f;
  box-shadow: 0 0 21px -1px #3c3c3c;
}
.logo {
  width: 130px;
}
.logo img {
  width: 100%;
}
.avatar img {
  width: 50px;
}
path {
  fill: transparent;
}

text {
  fill: red;
}
