.banner {
  color: #fff;
  object-fit: contain;
  height: auto;
  position: relative;
}
.banner::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  background: rgba(31, 31, 31, 0.3);
}
.banner::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 40%;
  bottom: 0;
  left: 0;
  background: linear-gradient(to top, rgb(19, 19, 19) 0%, rgba(89, 89, 89, 0));
}
.banner__contents {
  margin-left: 30px;
  padding: 85px 100px;
  height: auto;
  z-index: 10;
  position: relative;
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner__description {
  margin: 45px 0;
  font-size: 1.4rem;
  width: 45rem;
}
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 600;
  border-radius: 0.2vw;
  padding: 0.5rem 2rem;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
  transition: all 0.2s;
}
.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
}
@media (max-width: 415px) {
  .banner__contents {
    margin-left: 13px;
    padding: 50px 0;
  }
  .banner__description {
    max-width: 360px;
  }
}
@media (max-width: 768px) {
  .banner__contents {
    margin-left: 45px;
    padding: 70px 0;
  }
  .banner__description {
    max-width: 360px;
  }
}
