.row {
  display: flex;
  flex-direction: column;
  margin: 0 50px;
}
.row h1 {
  margin: 0 -25px;
}
.row-poster-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.row__posters {
  width: 100%;
  object-fit: contain;
  transition: all 0.3s ease;
}
.row__posters:hover {
  transform: scale(1.1);
}
.row-poster-wrapper-large {
  flex-wrap: nowrap;
  overflow-y: hidden;
  overflow-x: scroll;
  justify-content: flex-start;
}
.row-poster-wrapper::-webkit-scrollbar {
  display: none;
}
.row__posterslarge {
}
.movie-info-wrapper {
  position: relative;
  width: 100%;
  object-fit: contain;
  display: block;
  flex: 33.33% 1;
  max-width: 30.33%;
  margin: 10px 0;
  transition: all 0.3s ease;
  margin-right: 7px;
  overflow: hidden;
}
.movie-info-wrapper-large {
  min-width: 14%;
}

.movie-info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: all 0.3s ease;
  padding: 15px 0;
  overflow: hidden;
  cursor: pointer;
}
.movie-info .title {
  text-align: center;
  margin-bottom: 5px;
}
.movie-info .vote {
  font-size: 1.4rem;
  color: rgb(253, 253, 0);
  text-align: center;
  font-weight: 800;
}
.movie-info-wrapper:hover .movie-info {
  opacity: 1;
}
.movie-info .overview {
  padding: 0 23px;
  font-size: 1.1rem;
  line-height: 25px;
  text-align: center;
}
.watch {
  color: red;
  text-align: center;
  width: 45%;
  margin: 5px auto;
  background-color: #fff;
  border-radius: 5px;
  padding: 5px;
  font-weight: 700;
}
@media (max-width: 768px) {
  .row__posters {
    flex: 33.33%;
    max-width: 100%;
  }
  .movie-info-wrapper-large {
    min-width: 40%;
  }
  .movie-info-wrapper {
    max-width: 50%;
  }
}

@media (max-width: 415px) {
  .row__posters {
    flex: 100%;
    max-width: 100%;
  }
  .movie-info-wrapper-large {
    min-width: 88%;
  }
  .movie-info-wrapper {
    flex: 100%;
    max-width: 100%;
  }
  .banner__contents {
    margin-left: 0px;
  }
}
